___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 600px;
  margin: 0 auto;
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.digimon-card {
  border: 1px solid #ccc;
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 5px;
}

.filtro-container,
.filtroNome-container {
  margin-bottom: 20px;
}

/* Adicionando estilos para a classe 'filtrar' */
.filtrar,
.filtrarNome {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

/* Adicionando estilos para a classe 'btn-filtrar' e 'btn-filtrar-nome' */
.btn-filtrar,
.btn-filtrar-nome {
  background-color: #742de7;
  color: white;
  border: none;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

/* Estilizando o botão quando o cursor estiver sobre ele */
.btn-filtrar:hover,
.btn-filtrar-nome:hover {
  background-color: #4303aa;
}`, "",{"version":3,"sources":["webpack://./src/assets/css/style.css"],"names":[],"mappings":"AAAA;EACE,8BAA8B;EAC9B,yBAAyB;EACzB,SAAS;EACT,UAAU;AACZ;;AAEA;EACE,gBAAgB;EAChB,cAAc;EACd,sBAAsB;EACtB,aAAa;EACb,mBAAmB;EACnB,uCAAuC;AACzC;;AAEA;EACE,sBAAsB;EACtB,mBAAmB;EACnB,aAAa;EACb,kBAAkB;AACpB;;AAEA;;EAEE,mBAAmB;AACrB;;AAEA,gDAAgD;AAChD;;EAEE,aAAa;EACb,sBAAsB;EACtB,kBAAkB;EAClB,eAAe;AACjB;;AAEA,yEAAyE;AACzE;;EAEE,yBAAyB;EACzB,YAAY;EACZ,YAAY;EACZ,kBAAkB;EAClB,kBAAkB;EAClB,qBAAqB;EACrB,qBAAqB;EACrB,eAAe;EACf,kBAAkB;EAClB,eAAe;EACf,sCAAsC;AACxC;;AAEA,0DAA0D;AAC1D;;EAEE,yBAAyB;AAC3B","sourcesContent":["body {\n  font-family: Arial, sans-serif;\n  background-color: #f4f4f4;\n  margin: 0;\n  padding: 0;\n}\n\n.container {\n  max-width: 600px;\n  margin: 0 auto;\n  background-color: #fff;\n  padding: 20px;\n  border-radius: 10px;\n  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);\n}\n\n.digimon-card {\n  border: 1px solid #ccc;\n  margin-bottom: 10px;\n  padding: 10px;\n  border-radius: 5px;\n}\n\n.filtro-container,\n.filtroNome-container {\n  margin-bottom: 20px;\n}\n\n/* Adicionando estilos para a classe 'filtrar' */\n.filtrar,\n.filtrarNome {\n  padding: 10px;\n  border: 1px solid #ccc;\n  border-radius: 5px;\n  font-size: 16px;\n}\n\n/* Adicionando estilos para a classe 'btn-filtrar' e 'btn-filtrar-nome' */\n.btn-filtrar,\n.btn-filtrar-nome {\n  background-color: #742de7;\n  color: white;\n  border: none;\n  padding: 10px 20px;\n  text-align: center;\n  text-decoration: none;\n  display: inline-block;\n  font-size: 16px;\n  border-radius: 5px;\n  cursor: pointer;\n  transition: background-color 0.3s ease;\n}\n\n/* Estilizando o botão quando o cursor estiver sobre ele */\n.btn-filtrar:hover,\n.btn-filtrar-nome:hover {\n  background-color: #4303aa;\n}"],"sourceRoot":""}]);
